<template>
  <div class="flightsheet">
    <el-row class="newBtnRow" justify="end" v-if="!nonew">
      <el-col :span="3" :xs="24">
        <el-button type="primary" @click="newDialogVisible = true"><span class="mdi mdi-plus"></span> Nouveau</el-button>
      </el-col>
    </el-row>
    <el-empty description="No flight sheet found" v-if="flightsheets.length === 0"/>
    <div v-else ref="fs_collapse">
      <el-collapse class="flight-collapses" v-for="(sheet, idx) in flightsheets" :key="sheet">
        <el-collapse-item :name="idx" class="flight-collapse">
          <template #title>
            <el-row style="align-items: center; width: 100%;">
              <el-col :span="22" style="text-align: left;">
                <el-space direction="horizontal">
                  <h4><span class="mdi mdi-ethereum"></span> {{sheet.coin}}</h4>
                  <p>{{sheet.name}}</p>
                  <p>{{sheet.pool.name}}</p>
                  <p>{{sheet.miner}}</p>
                </el-space>
              </el-col>
              <el-col :span="2">
                <slot name="action" v-bind:fs="sheet"></slot>
              </el-col>
            </el-row>
          </template>
          <el-descriptions title="Flight Sheet infos" direction="vertical">
            <el-descriptions-item label="Wallet Address:" label-class-name="flightsheet-label">
              <span class="flightsheet-desc-info">{{sheet.wallet[0].address}}</span>
            </el-descriptions-item>
            <el-descriptions-item :label="sheet.pool.name + ' URLs:'" label-class-name="flightsheet-label">
              <div v-for="url in sheet.pool.urls" :key="url">
                <span class="flightsheet-desc-info">{{url}}</span>
              </div>
            </el-descriptions-item>
          </el-descriptions>
          <el-button type="danger" size="mini" style="float: right; margin-top: 10px; margin-bottom: 10px;"
            v-if="!nodelete" @click="() => deleteFlightsheet(idx)">Delete</el-button>
        </el-collapse-item>
      </el-collapse>
    </div>
    <el-dialog title="New flightsheet" v-model="newDialogVisible" v-if="!nonew">
      <el-form :model="form" label-width="120px" inline>
        <el-form-item label="Coin:" required>
          <el-select v-model="form.coin">
            <el-option v-for="item in form.coinOptions" :key="item"
              :label="item" :value="item"/>
          </el-select>
        </el-form-item>
        <el-form-item label="Wallet:" required>
          <el-select v-model="form.wallet" :disabled="form.coin === ''">
            <el-option v-for="item in walletsFromCoin" :key="item" :label="item.name" :value="item.address" style="height: 50px;">
              <div style="height: 20px;">{{item.name}}</div>
              <div style="font-size: 12px; color: var(--el-color-info)">{{item.address}}</div>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Pool:" required>
          <el-select v-model="form.pool" :disabled="form.wallet === ''">
            <el-option v-for="item in form.poolOptions" :key="item" :label="item.name" :value="item.name"/>
          </el-select>
        </el-form-item>
        <el-button size="mini" icon="mdi mdi-cog" plain round v-if="form.pool !== ''"
          @click="poolUrlsDialogVisible = true"></el-button>
        <el-form-item label="Miner:" required>
          <el-select v-model="form.miner" :disabled="form.pool === ''">
            <el-option v-for="item in form.minerOptions" :key="item" :label="item" :value="item"/>
          </el-select>
        </el-form-item>
        <el-form-item label="Name:" :error="form.error">
          <el-input type="text" placeholder="Enter flightsheet name" v-model="form.name"/>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="newDialogVisible = false">Cancel</el-button>
        <el-button type="primary" :disabled="saveDisabled" @click="saveFlightSheet">Save</el-button>
      </template>
    </el-dialog>
    <el-dialog v-model="poolUrlsDialogVisible" title="Select URLs" v-if="!nonew">
      <el-checkbox-group v-model="form.poolUrlsSelected" >
        <div v-for="item in urlsFromPool" :key="item">
          <el-checkbox :label="item"/>
        </div>
      </el-checkbox-group>
      <template #footer>
        <el-button type="primary" @click="poolUrlsDialogVisible = false">Save</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import api from "../api/antos-api"
export default {
  name: "Flightsheet",
  props: {
    flightsheets: Array,
    nonew: Boolean,
    wallets: Array,
    nodelete: Boolean
  },
  data() {
    return {
      newDialogVisible: false,
      form: {
        coin: '',
        wallet: '',
        pool: '',
        miner: '',
        name: '',
        poolUrlsSelected: [],
        coinOptions: ['ETH'],
        poolOptions: [],
        minerOptions: ['nanominer'],
        error: ''
      },
      poolUrlsDialogVisible: false
    }
  },
  computed: {
    walletsFromCoin() {
      return this.wallets.filter(value => value.coin === this.form.coin)
    },
    urlsFromPool() {
      return this.form.poolOptions.filter(value => value.name === this.form.pool).map(value => value.urls)[0]
    },
    saveDisabled() {
      return this.form.coin === '' || this.form.wallet === '' || this.form.pool === '' || this.form.miner === '' ||
          this.form.poolUrlsSelected.length === 0
    }
  },
  mounted() {
    if(!this.nonew) {
      api.get_pools().then(resp => {
        this.form.poolOptions = resp.data
      })
    }
  },
  methods: {
    saveFlightSheet() {
      const fs = {
        name: this.form.name,
        wallet_id: this.wallets.filter(value => value.address === this.form.wallet).map(value => value._id.$oid)[0],
        pool: {name: this.form.pool, urls: this.form.poolUrlsSelected},
        miner: this.form.miner,
        coin: this.form.coin
      }
      api.post_farm_flightsheet(this.$route.params.id, fs).then(resp => {
        if(resp.status === 201) {
          this.$emit('added')
        }
      }).catch(() => {
        this.form.error = 'This name already exists'
      })
    },
    deleteFlightsheet(index) {
      api.delete_flightsheet(this.flightsheets[index]._id.$oid).then(resp => {
        if(resp.status === 200) {
          this.$emit('deleted')
        }
      })
    }
  }
}
</script>

<style scoped>
.flight-collapse {
  margin: 10px;
}

.flight-collapses {
  background-color: white;
}

.newBtnRow {
  margin-bottom: 10px;
}

.flightsheet-desc-info {
  color: var(--el-color-info);
}
</style>
<style>
.flightsheet-label {
  color: black;
}
</style>